import * as THREE from 'three'
import CircleLineGeometry from './CircleLineGeometry'

export default function Pulse(radius, color, duration = 5000, autoStart = true) {
  THREE.Object3D.call(this);

  this.duration = duration;
  const segments = 32;
  // const discColor = color;
  const lineColor = color;

  // const discGeometry = new THREE.CircleGeometry(radius, segments);
  // const discMaterial = new THREE.MeshLambertMaterial({
  //   side: THREE.DoubleSide,
  //   color: discColor,
  //   transparent: true,
  //   opacity: 1,
  // });
  // this.disc = new THREE.Mesh(discGeometry, discMaterial);
  // this.add(this.disc);

  const lineGeometry = new CircleLineGeometry(radius, segments);
  const lineMaterial = new THREE.LineBasicMaterial({
    color: lineColor,
    linewidth: 1,
    transparent: true,
    opacity: 1,
  });
  this.line = new THREE.Line(lineGeometry, lineMaterial);
  this.add(this.line);

  if (autoStart) {
    this.start();
  }
}

Pulse.prototype = Object.assign(Object.create(THREE.Object3D.prototype), {

  constructor: Pulse,

  start() {
    this.started = new Date()
    // Adding random starting time
    this.started.setSeconds(this.started.getSeconds() + THREE.Math.randFloat(-1,1));
  },

  stop() {
    this.started = null;
  },

  hide() {
    this.line.material.opacity = 0
    // this.disc.material.opacity = 0
  },

  update() {
    if (!this.started) {
      return;
    }
    const now = new Date();
    let t = (now - this.started) / this.duration;
    if (t >= 1) {
      t -= Math.floor(t);
    }

    const opacity = 1 - t*t*t*t;

    let scale = t <= 0 ? 0.001 : t

    // I was getting an error:
    //  Matrix3.getInverse(): can't invert matrix, determinant is 0
    //
    // From SO:
    //  Matrix3.getInverse(): can't invert matrix, determinant is 0
    //  usually happens when either the scale.x, scale.y or scale.z are 0.
    //  Make sure you're not scaling the object to 0.
    //
    // Ref:
    //  http://stackoverflow.com/questions/19150120/scaling-an-object-in-three-js

    this.scale.set(scale, scale, scale);
    this.line.material.opacity = opacity;
    // this.disc.material.opacity = opacity * 0.5;
  },

});