import { connect } from 'react-redux';
import { fetchGraceNoteData, selectResult, setPOI, unsetPOI, unselectResult, setFilters } from '../actions';
import App from '../components/App';

const mapStateToProps = state => {
  return {
    phaseResults: state.phaseResults,
    selectedResult: state.selectedResult,
    globeStatus: state.globeStatus,
    filterOptions: state.filterOptions
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchGraceNoteData: () => {
      dispatch(fetchGraceNoteData());
    },
    onSelectResult: result => {
      dispatch(selectResult(result))
    },
    onUnselectResult: () => {
      dispatch(unselectResult())
    },
    setPOI: (lat, lng) => {
      dispatch(setPOI(lat, lng))
    },
    unsetPOI: () => {
      dispatch(unsetPOI())
    },
    setFilters: (newFilterValues) => {
      dispatch(setFilters(newFilterValues))
    }
  };
};

const AppContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(App);

export default AppContainer;
