import React, { Component } from 'react'

class AthleteFilters extends Component {
  constructor(props) {
    super(props)
    this.state = {
      sport: '',
      gender: '',
      medal: '',
      name: '',
      sortby: ''
    }

    this.onChange = this.onChange.bind(this)
    this.onReset = this.onReset.bind(this)
  }

  onChange() {
    const { setFilters } = this.props

    const newFilterValues = {
      sport:  document.getElementById("sport-name").value,
      gender: document.getElementById("sport-gender").value,
      medal: document.getElementById("sport-medal").value,
      name: document.getElementById("athlete-name").value
    }

    return this.setState({
      sport: newFilterValues.sport,
      gender: newFilterValues.gender,
      medal: newFilterValues.medal,
      name: newFilterValues.name,
    }, setFilters(newFilterValues))
  }

  onReset() {
    const { setFilters } = this.props
    const resetValues = {
      sport: 'All',
      gender: 'All',
      medal: 'All',
      name: ''
    }
    return this.setState({
      sport: resetValues.sport,
      gender: resetValues.gender,
      medal: resetValues.medal,
      name: resetValues.name
    }, setFilters(resetValues))
  }

  render() {
    const { filterOptions } = this.props
    const sportNames = filterOptions.sport.map(x => (<option key={x} value={x}>{x}</option>))
    const genderNames = filterOptions.gender.map(x => (<option key={x} value={x}>{x}</option>))
    const medalNames = filterOptions.medal.map(x => (<option key={x} value={x}>{x}</option>))
    return (
      <div>
        <div className="form-row">
          <div className="col-6">
            <div className="form-group">
              <label htmlFor="sport-name">Sport</label>
              <select id="sport-name" value={this.state.sport} className="form-control" onChange={this.onChange}>
                {sportNames}
              </select>
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sport-gender">Gender</label>
              <select id="sport-gender" value={this.state.gender} className="form-control" onChange={this.onChange}>
                {genderNames}
              </select>
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sport-medal">Medal</label>
              <select id="sport-medal" value={this.state.medal} className="form-control" onChange={this.onChange}>
                {medalNames}
              </select>
            </div>
          </div>
        </div>
        <div className="form-row">
          <div className="col-9">
            <div className="form-group">
              <label htmlFor="athlete-name">Athelete name</label>
              <input id="athlete-name" value={this.state.name} type="text" className="form-control" onChange={this.onChange} />
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sort-by">Sort by</label>
              <select id="sort-by" value={this.state.sortby} className="form-control" onChange={this.onChange}>
                <option value="time-desc">Newest first</option>
                <option value="time-asc">Oldest first</option>
                <option value="name-asc">Name (A-Z)</option>
                <option value="name-desc">Name (Z-A)</option>
              </select>
            </div>
          </div>
        </div>

        <div className="form-group">
          <button className="btn btn-light" onClick={this.onReset}>Reset filters</button>
        </div>
      </div>
     )
  }
}

export default AthleteFilters