import React, { Component } from 'react';
import * as THREE from 'three'
import * as TWEEN from '@tweenjs/tween.js'
import OrbitControls from 'threejs-orbit-controls'
import Pulse from './Pulse'

class MyGlobe extends Component {
	constructor(props) {
		super(props);
		this.renderer = new THREE.WebGLRenderer()
		this.scene = new THREE.Scene()
		this.container = null
		this.camera = {
			object: null, // camera object
			controls: null, // orbitControls object
			fov: 60,
			altitude: 400,
			initialDistance: 0
		}

		this.globeProps = {
			globeRadius: 200,
			widthSegments: 50,
			heightSegments: 50
		}

		this.globeGroup = new THREE.Group()
		this.circleGroup = new THREE.Group()
		this.markerObject = new THREE.Object3D()

		this.raycaster = new THREE.Raycaster()
		this.mouse = new THREE.Vector2()

		this.state = {
			spinning: true
		}
	}

	componentDidUpdate(prevProps) {
		const { phaseResults, selectedResult, globeStatus } = this.props

		this.updatePhaseResults(phaseResults)

		if (prevProps.selectedResult !== undefined) {
			if (selectedResult.n_PhaseResultID !== prevProps.selectedResult.n_PhaseResultID) {
				this.rotateCamera(selectedResult.geojson.lat, selectedResult.geojson.lng, 1.4)
			}
		}

		if (prevProps.globeStatus.viewDetails === true && globeStatus.viewDetails === false) {
			this.zoomOut()
		}


		if (globeStatus.viewDetails === true) {
			this.highlightCircle(selectedResult)
		} else {
			this.unHighlightCircle(phaseResults)
		}
	}

	componentDidMount() {
		const { setPOI, unsetPOI } = this.props
		const _this = this
		this.container = document.getElementById('my-globe')
		const WIDTH = this.container.clientWidth
		const HEIGHT = this.container.clientHeight

		/** Renderer */
		this.renderer.setSize(WIDTH, HEIGHT)

		/** Camera */
		this.camera.object = new THREE.PerspectiveCamera(this.camera.fov, WIDTH / HEIGHT, 1, 10000)
		this.camera.object.position.set(0, 200, this.camera.altitude)
		this.camera.object.lookAt(new THREE.Vector3(0, 0, 0))
		this.camera.initialDistance = this.camera.object.position.distanceTo(new THREE.Vector3(0,0,0))
		this.camera.controls = new OrbitControls(this.camera.object, this.container)
		this.camera.controls.autoRotate = this.state.spinning
		this.camera.controls.autoRotateSpeed = .5

		/** Scene */
		this.scene.background = new THREE.Color( 0x000 )
		this.scene.add(this.camera.object)
		this.container.appendChild(this.renderer.domElement)

		/** Lighting */
		const ambientLight = new THREE.AmbientLight(0x404040)
		this.scene.add(ambientLight)
		const spotLight = new THREE.PointLight(0xffffff);
		spotLight.position.set(500, 500, 500);
		this.camera.object.add(spotLight);

		/** Objects in scene */
		const loader = new THREE.TextureLoader()
		loader.load( 'https://another-quang.github.io/build/assets/day_2048.jpg', texture => {
			const sphere = new THREE.SphereGeometry(this.globeProps.globeRadius, this.globeProps.widthSegments, this.globeProps.heightSegments)
			const material = new THREE.MeshLambertMaterial({
				map: texture,
				// color: 0xffffff,
				// wireframe: true
			})
			const mesh = new THREE.Mesh(sphere, material)
			mesh.name = 'globe'
			this.globeGroup.add(mesh)
		})
		this.scene.add(this.globeGroup)
		this.scene.add(this.circleGroup)
		this.scene.add(this.markerObject)
		// this.globeGroup.add(new THREE.AxesHelper(1000))
		// this.globeGroup.add(new THREE.GridHelper(1000, 10, 0x0000ff, 0x808080))

		/** Mouse interaction */
		let drag = false
		window.addEventListener('mousedown', () => drag = false)
		window.addEventListener('mousemove', () => drag = true)
		window.addEventListener('mouseup', (event) => {
			if (drag === false) {
				const mouseOffset = document.getElementById('athlete-list')
				const globeObject = _this.globeGroup.getObjectByName('globe')
				const canvasWidth = window.innerWidth - mouseOffset.clientWidth
				if (event.clientX < canvasWidth) {
					_this.mouse.x = (event.clientX / canvasWidth) * 2 - 1
					_this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1
					_this.raycaster.setFromCamera(_this.mouse, _this.camera.object)
					_this.markerObject.children.forEach(x => _this.markerObject.remove(x))

					while(_this.markerObject.children.length > 0) {
						_this.markerObject.remove(_this.markerObject.children[0])
					}

					const intersects = _this.raycaster.intersectObject(globeObject)
					if (intersects.length === 0) {
						_this.zoomOut()
						unsetPOI()
						return _this.setState({ spinning: true }, () => _this.applyStatus())
					}
					const pointOfIntersection = intersects[0].point
					_this.addMarkerPOI(pointOfIntersection)
					const { lat, lng } = _this.calculateLatLngCoordinates(pointOfIntersection)
					_this.rotateCamera(lat, lng, 1.2)
					setPOI(lat, lng)
				}
			}
		})

		const centerDistance = this.camera.object.position.distanceTo(new THREE.Vector3(0, 0, 0))
		const tangentDistance = Math.cos(Math.PI / 180 * this.camera.fov / 2) * centerDistance

		/** Animation */
		function render () {
			requestAnimationFrame(render)
			_this.circleGroup.children.forEach(circle => {
				if (_this.camera.object.position.distanceTo(circle.position) < tangentDistance) {
					circle.update()
				} else {
					circle.hide()
				}
			})
			_this.markerObject.rotateZ(Math.PI / 100)
			TWEEN.update()
			_this.camera.controls.update()
			_this.renderer.render(_this.scene, _this.camera.object)
		}

		render()
	}

	applyStatus() {
		this.camera.controls.autoRotate = this.state.spinning
	}

	highlightCircle(selectedResult) {
		const circle = this.circleGroup.getObjectByName(selectedResult.n_PhaseResultID)
		if (typeof(circle) !== 'undefined') {
			this.circleGroup.children.forEach(c => c.visible = false)
			circle.visible = true
		}
	}

	unHighlightCircle(phaseResults) {
		const _this = this
		const visibleResults = phaseResults.poi.length > 0 ? phaseResults.poi : phaseResults.filtered
		visibleResults.forEach(x => _this.circleGroup.getObjectByName(x.n_PhaseResultID).visible = true)
	}

	rotateCamera(lat, lng, zoomFactor = 1) {
		const _this = this
		this.setState({ spinning: false }, () => _this.applyStatus())


		const target = this.calculate3dCoordinates(lat, lng, this.globeProps.globeRadius)

		const coeff = this.camera.altitude / this.globeProps.globeRadius / zoomFactor
		let from = {
			x: this.camera.object.position.x,
			y: this.camera.object.position.y,
			z: this.camera.object.position.z
		}

		const to = {
			x: target.x * coeff,
			y: target.y * coeff,
			z: target.z * coeff
		}

		return new TWEEN.Tween(from)
			.to(to, 2000)
			.easing(TWEEN.Easing.Quartic.Out)
			.onUpdate(() => {
				_this.camera.object.position.copy(from);
				_this.camera.object.lookAt(new THREE.Vector3(0, 0, 0));
			})
			.start()
	}

	zoomOut() {
		const _this = this
		const distanceToCenter = this.camera.object.position.distanceTo(new THREE.Vector3(0, 0, 0))
		const coeff = this.camera.initialDistance / distanceToCenter

		let from = {
			x: this.camera.object.position.x,
			y: this.camera.object.position.y,
			z: this.camera.object.position.z
		}

		const to = {
			x: this.camera.object.position.x * coeff,
			y: this.camera.object.position.y * coeff,
			z: this.camera.object.position.z * coeff
		}

		return new TWEEN.Tween(from)
			.to(to, 2000)
			.easing(TWEEN.Easing.Quartic.Out)
			.onUpdate(() => {
				_this.camera.object.position.copy(from);
				_this.camera.object.lookAt(new THREE.Vector3(0, 0, 0));
			})
			.start()
	}

	updatePhaseResults(phaseResults) {
		var _this = this
		if (_this.circleGroup.children.length === 0) {
			// Draw all circles
			phaseResults.all.forEach(element => {
				if (element.geojson !== undefined) {
					_this.addCircle(element)
				}
			})
		} else {
			// Hide all circles and show only what's filtered or poi'ed
			this.circleGroup.children.forEach(x => x.visible = false)
			const visibleResults = phaseResults.poi.length > 0 ? phaseResults.poi : phaseResults.filtered
			visibleResults.forEach(x => _this.circleGroup.getObjectByName(x.n_PhaseResultID).visible = true)
		}
	}

	addMarkerPOI(position) {
		const spherical = new THREE.Spherical()
		spherical.setFromVector3(position)
		spherical.radius += .2

		const newPosition = new THREE.Vector3()
		newPosition.setFromSpherical(spherical)

		const distanceThreshold = 20
		const circleMaterial = new THREE.MeshBasicMaterial({
			side: THREE.DoubleSide,
			color: 0x9cff57,
			opacity: 0.5,
			transparent: true
		})
		const point = new THREE.Mesh(new THREE.CircleGeometry(0.5, 32), circleMaterial)
		this.markerObject.add(point)

		const pie = new THREE.Mesh(new THREE.CircleGeometry(distanceThreshold, 32, 0, Math.PI / 10), circleMaterial)
		this.markerObject.add(pie)

		// Set poistion and face for marker
		this.markerObject.position.copy(newPosition)
		this.markerObject.lookAt(new THREE.Vector3(0 ,0, 0))
	}

	addCircle(element) {
		const center = this.calculate3dCoordinates(element.geojson.lat + THREE.Math.randFloat(-2, 2), element.geojson.lng + THREE.Math.randFloat(-2, 2), this.globeProps.globeRadius)
		const medalColor = element.n_RankMedal === 1 ? 0xffff00 : (element.n_RankMedal === 2 ? 0xC0C0C0 : 0xCD7F32)
		const radius = THREE.Math.randFloat(5, 10)
		let pulse = new Pulse(radius, medalColor)
		pulse.name = element.n_PhaseResultID
		pulse.position.copy(center)
		pulse.lookAt(new THREE.Vector3(0, 0, 0))
		this.circleGroup.add(pulse)
	}

	calculateLatLngCoordinates(threeVector) {
		const spherical = new THREE.Spherical()
		spherical.setFromVector3(threeVector)
		const lat = THREE.Math.radToDeg(Math.PI / 2 - spherical.phi)
		let lng = THREE.Math.radToDeg(spherical.theta - Math.PI / 2)
		lng = lng < -180 ? lng + 360 : lng
		return {lat, lng}
	}

	calculate3dCoordinates(lat, lng, radius) {
		const phi   = (90 - parseInt(lat, 10)) * (Math.PI / 180)
		const theta = (parseInt(lng, 10) + 180) * (Math.PI / 180)
		const x = -((radius) * Math.sin(phi) * Math.cos(theta))
		const z = ((radius) * Math.sin(phi) * Math.sin(theta))
		const y = ((radius) * Math.cos(phi))

		return new THREE.Vector3(x, y, z)
	}

	render() {
		return ( <div id="my-globe"></div> );
	}
}

export default MyGlobe;
