import React, { Component } from 'react'
import Globe from './Globe/Globe'
import AthleteList from './AthleteList/AthleteList'
import '../vendors/stylesheets/bootstrap/bootstrap.scss'
import './App.scss'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loaded: false
    }
  }

  componentDidMount() {
    if (this.state.loaded === false) {
      this.props.fetchGraceNoteData()
      this.setState({ loaded: true })
    }
  }

  render() {
    const { phaseResults, selectedResult, onSelectResult, onUnselectResult, setPOI, unsetPOI, globeStatus, filterOptions, setFilters } = this.props
    return (
      <div className="container-fluid p-0">
        <div className="row no-gutters">
          <div className="col-lg-8">
            <Globe
              phaseResults={phaseResults}
              selectedResult={selectedResult}
              globeStatus={globeStatus}
              setPOI={setPOI}
              unsetPOI={unsetPOI}
              />
          </div>
          <div className="col-lg-4">
            <AthleteList
              phaseResults={phaseResults}
              selectedResult={selectedResult}
              onSelectResult={onSelectResult}
              onUnselectResult={onUnselectResult}
              globeStatus={globeStatus}
              filterOptions={filterOptions}
              setFilters={setFilters}
              />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
