import React, { Component } from 'react'
import MyGlobe from './MyGlobe'
import './Globe.scss'

class Globe extends Component {
  render() {
    const {phaseResults, selectedResult, globeStatus, setVisibleResults, setPOI, unsetPOI,onUnselectResult} = this.props
    return (
      <MyGlobe
        phaseResults={phaseResults}
        selectedResult={selectedResult}
        globeStatus={globeStatus}
        setVisibleResults={setVisibleResults}
        setPOI={setPOI}
        unsetPOI={unsetPOI}
        onUnselectResult={onUnselectResult}
        />
    );
  }
}

export default Globe;