import { RECEIVE_GRACENOTE_DATA } from '../constants/ActionTypes'

const initialState = {
  sport: [],
  gender: [],
  medal: [],
}

const uniq = (array) => {
  return [...new Set(array)]
}

const getAvailableFields = (results, fieldName) => {
  if (results.length > 0) {
    const temp = uniq(results.map(x => x[fieldName])).sort()
    temp.unshift('All')
    return temp
  }
}

const filterOptions = (state = initialState, action) => {
  switch (action.type) {
    case RECEIVE_GRACENOTE_DATA: {
      return Object.assign({}, state, {
        sport: getAvailableFields(action.json, 'c_Sportname'),
        gender: getAvailableFields(action.json, 'c_EventGender'),
        medal: getAvailableFields(action.json, 'n_RankMedal'),
      })
    }

    default:
      return state
  }
};

export default filterOptions
